<template>
    <div class="page">
        <vue-topprogress ref="topProgress"></vue-topprogress>
        <smm_navbar pageName="photo-measuring"/>
        <smmLoader v-if="reqPending">
            <div slot="smmText" class="smm-text">
                Please wait, facial blur privacy protection technology in progress.
            </div>
        </smmLoader>
        <div class="page-patient-details page-container pb-0">
            <div :class="overlay"></div>
            <div class="container">
                <div class="row m-0">
                    <div class="col d-flex flex-column  align-items-center position-relative">
                        <input type="file" accept="image/*;capture=camera" @change="onFileChange">

                        <div class="mt-4" v-show="!isHide">
                            <video ref="video" class="w-100" id="video" autoplay></video>
                        </div>
                        <img v-show="isHide" ref="image" class="img-fluid mt-4" :src="captures"/>
                        <img class="hide-img" id="hide-img" src="../../assets/pixelated.jpg">
                        <canvas ref="canvas" id="canvas" width="640" height="480"></canvas>
                        <div>
                            <div class="round-button p-0">
                                <div class="next submit-button">
                                    <button v-if="!isHide" type="button" @click="capture">Click</button>
                                    <button v-if="isHide" class="mr-4 retakeLink" type="button"  @click="retake">Retake</button>
                                    <button v-if="isHide" class="done" type="button" @click="goBack">Done</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div>
                    <b-modal ref="myModalRef" class="text-dark text-center" hide-footer
                             title="Message">
                        <div class="d-block text-center text-dark">
                            <p>{{message}}</p>
                        </div>
                        <b-button class="mt-3 mr-3 ml-auto btn text-center text-danger modal-button"
                                  variant="outline-danger"
                                  v-if="sowUseImageButton"
                                  @click="useImage">Use This Image
                        </b-button>
                        <b-button class="mt-3 mr-3 ml-auto btn w-25 text-center text-danger modal-button"
                                  variant="outline-danger"
                                  @click="hideModal">{{buttonText}}
                        </b-button>
                    </b-modal>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as MobileDetect from 'mobile-detect'
    import smm_navbar from '../navbar.vue';
    import {endPoints} from "../../api/endpoints";
    import {saveData} from "../../provider/savedataMixin";
    import {saveMethods} from "../../provider/saveMethod";

    export default {
        name: "image_view",
        components: {
            smm_navbar
        },
        data() {
            return {
                video: {},
                canvas: {},
                captures: "",
                isHide: false,
                context: '',
                blurImage: '',
                side: 'side',
                userType: '',
                message: 'Unable to detect facial recognition, Please retake image to blur the eyes',
                photo: null,
                stream: '',
                hasUserMedia: false,
                overlay:'',
                sowUseImageButton: false,
                buttonText: '',
                reqPending: false,
                platform:'',
                dyWidth:'',
                dyHeight: ''
            }
        },
        mounted() {
            console.log(MobileDetect);
            var md = new MobileDetect();
            console.log(md.mobile());
            this.video = this.$refs.video;
                        /*if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
                            navigator.mediaDevices.getUserMedia({video: true}).then(stream => {
                                // console.log("stream: ", window.URL);
                                this.video.srcObject  = stream;
                                this.video.play();
                            });
                        }*/


                        navigator.getUserMedia = (navigator.getUserMedia ||
                          navigator.webkitGetUserMedia ||
                          navigator.mozGetUserMedia ||
                          navigator.msGetUserMedia ||
                          navigator.oGetUserMedia);

                        // new api for video and audio called navigator.mediadevices
                        // for new browsers video can directly consume MediaStream using srcObject
                        //
                        if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
                          navigator.mediaDevices
                            .getUserMedia({video: true})
                            .then(stream => {
                              this.video = this.$refs.video; // reInitializing as it doesn't work sometime.
                              this.video.srcObject = stream;
                              this.stream = stream;
                              this.hasUserMedia = true;
                            })
                            .catch(error => {
                              console.log(error);
                    });
            } else if (navigator.getUserMedia) {
                // if new api not there, use the old one
                navigator.getUserMedia(
                    {video: true},
                    stream => {
                        this.video = this.$refs.video;
                        // below line won't work on new browser because of this
                        // https://developer.mozilla.org/en-US/docs/Web/API/URL/createObjectURL#Using_object_URLs_for_media_streams
                        // this.src = window.URL.createObjectURL(stream);
                        this.video.srcObject = stream;
                        this.stream = stream;
                        this.hasUserMedia = true;
                    },
                    error => {
                        console.log(error);
                    }
                );
            }else{
                this.buttonText = "Close";
                this.message= 'Your browser is not support webcam';
            }

        },
        methods: {
            retake(){
              this.isHide = false;
              this.overlay = 'remove-overlay';
            },
            onFileChange(e) {
                console.log(e);
                var portraitImage = false;

                var that = this;

                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                var reader = new FileReader();
                var preview = document.createElement('img');
                reader.addEventListener("load", function () {
                    preview.src = reader.result;
                    console.log(reader.result);
                    preview.addEventListener("load", function () {
                        console.log(preview.height);
                        console.log(preview.width);
                        console.log(that.side);
                        console.log(preview.height > preview.width);
                        if(that.side === 'front') {
                            if (preview.height > preview.width) {
                                // you're in PORTRAIT mode
                                alert('Oooops, looks like you have taken the photo in portrait mode, ' +
                                    'please rotate your device and re-take in landscape.');
                                portraitImage = true;
                                return false;
                            }
                        }



                        if (!portraitImage){
                            that.canvas = that.$refs.canvas;
                            let height = preview.height;
                            let width = preview.width;
                            that.dyWidth = width;
                            that.dyHeight = height;
                            that.canvas.width = width;
                            that.canvas.height = height;
                            that.context = that.canvas.getContext("2d").drawImage(preview, 0, 0, width, height);
                            that.captures = canvas.toDataURL("image/png");
                            that.isHide = true;
                            that.$refs.topProgress.start();
                            that.overlay = 'add-overlay';
                            that.getCords(that.captures);
                        }else{
                            // captured image is in portrait
                            that.isHide = false;
                            that.overlay = 'remove-overlay';
                            alert('Oooops, looks like you have taken the photo in portrait mode, please rotate your device and re-take in landscape.');
                        }



                    })
                }, false);
                reader.readAsDataURL(files[0]);

            },
            capture() {
              var portraitImage = false;
              if(this.side === 'front'){
                var capturedImage = this.$refs.video;
                if (capturedImage.width < capturedImage.height){
                  // captured image is in portrait
                 // this.isHide = false;
                 // this.overlay = 'remove-overlay';
                  portraitImage = true;
                  // alert('Oooops, looks like you have taken the photo in portrait mode, ' +
                  //     'please rotate your device and re-take in landscape.');
                }
              }
              if(this.side === 'front') {
                  if (window.matchMedia("(orientation: portrait)").matches) {
                      // you're in PORTRAIT mode
                      alert('Oooops, looks like you have taken the photo in portrait mode, ' +
                          'please rotate your device and re-take in landscape.');
                      portraitImage = true;
                      return false;
                  }
              }




              if (!portraitImage){
                this.canvas = this.$refs.canvas;
                let height = this.video.videoHeight;
                let width = this.video.videoWidth;
                this.dyWidth = width;
                this.dyHeight = height;
                this.canvas.width = width;
                this.canvas.height = height;
                console.log(this.canvas);
                this.context = this.canvas.getContext("2d").drawImage(this.video, 0, 0, width, height);
                this.captures = canvas.toDataURL("image/png");
                this.isHide = true;
                this.$refs.topProgress.start();
                this.overlay = 'add-overlay';
                this.getCords(this.captures);
              }else{
                // captured image is in portrait
                this.isHide = false;
                this.overlay = 'remove-overlay';
                alert('Oooops, looks like you have taken the photo in portrait mode, please rotate your device and re-take in landscape.');
              }
            },
            async getCords(img) {
                let axiosConfig = {
                    'Access-Control-Allow-Origin': '*',
                    'Content-Type': 'application/json'
                };

                let sentImage = {image: img};
                this.reqPending = true;
                await fetch(endPoints.baseUrl + endPoints.getimage, {
                    method: 'post',
                    headers: axiosConfig,
                    body: JSON.stringify(sentImage)
                }).then(response => {
                    return response.json();
                }).then((data) => {
                    this.setPixlate(data);
                    this.reqPending = false;
                })
                    .catch((error) => {
                        this.reqPending = false;
                        console.log('Request failed', error);
                         this.message= 'This request has been blocked; the content must be served over HTTPS';
                         this.buttonText = "Done";
                          this.overlay = 'remove-overlay';
                          this.$refs.topProgress.done();
                          this.$refs.myModalRef.show();
                    });
            },

            take_photo() {
                this.photo = this.$refs.webcam.getPhoto();
            },

            setPixlate(obj) {
                if (obj.cords.length) {
                    var eye_cords = obj.cords;

                    let img = this.$refs.image;

                    // let imgHeight = img.height;
                    // let imgWidth = img.width;
                    let imgHeight = this.dyHeight;
                    let imgWidth = this.dyWidth;

                    var hideImage = document.getElementById('hide-img').getAttribute('src');
                    this.blurImage = new Image();
                    this.blurImage.src = `${hideImage}`;

                    let canvasImg = this.$refs.canvas;
                    let contextImg = canvasImg.getContext("2d");
                    contextImg.drawImage(img, 0, 0, imgWidth, imgHeight);

                    let x, y, width, height;
                    for (var key in eye_cords) {
                        if (this.side === "front") {
                            if (eye_cords[key].left_eye.width > 0) {
                                x = eye_cords[key].left_eye.left_of_left_eyebrow.x - 10;
                                y = (eye_cords[key].left_eye.right_of_left_eyebrow.y - 25);
                                width = (eye_cords[key].right_eye.right_of_right_eyebrow.x - eye_cords[key].left_eye.left_of_left_eyebrow.x) +25  ;
                                height = eye_cords[key].left_eye.width + 30;
                                this.drawBlurImage(contextImg, x, y, width, height);
                            }
                            if (eye_cords[key].nose.width_left > 0) {
                                x = (eye_cords[key].mouth.mouth_center.x - 20);
                                y = ((eye_cords[key].between_eye.y + (eye_cords[key].left_eye.width / 2)) - 10);
                                width = ((eye_cords[key].mouth.width_right + 60)) ;
                                height = ((eye_cords[key].mouth.mouth_center.y - eye_cords[key].between_eye.y) + 10);
                                this.drawBlurImage(contextImg, x, y, width, height);
                            }
                            if (eye_cords[key].nose.width_left > 0) {
                                x = ((eye_cords[key].mouth.mouth_center.x - eye_cords[key].mouth.width_right) - 45);
                                y = ((eye_cords[key].between_eye.y + (eye_cords[key].left_eye.width / 2)) - 10);
                                width = ((eye_cords[key].mouth.width_right + 40));
                                height = ((eye_cords[key].mouth.mouth_center.y - eye_cords[key].between_eye.y) + 10);
                                this.drawBlurImage(contextImg, x, y, width, height);
                            }
                        } else {
                            if (eye_cords[key].left_eye.width > 0) {
                                x = eye_cords[key].left_eye.left_of_left_eyebrow.x - 40;
                                y = (eye_cords[key].left_eye.right_of_left_eyebrow.y - 30);
                                width = (eye_cords[key].nose.nose_tip.x - eye_cords[key].left_eye.left_of_left_eyebrow.x) + 50;
                                height = ((eye_cords[key].mouth.mouth_center.y - eye_cords[key].left_eye.right_of_left_eyebrow.y) + 70);
                                this.drawBlurImage(contextImg, x, y, width, height);
                            }
                            if (eye_cords[key].right_eye.width > 0) {
                                x = ((eye_cords[key].right_eye.left_of_right_eyebrow.x - (eye_cords[key].nose.nose_tip.x - eye_cords[key].right_eye.right_of_right_eyebrow.x)) - 20);
                                y = (eye_cords[key].right_eye.right_of_right_eyebrow.y - 30) ;
                                width = (eye_cords[key].nose.nose_tip.x - eye_cords[key].right_eye.right_of_right_eyebrow.x) - 60;
                                height = ((eye_cords[key].mouth.mouth_center.y - eye_cords[key].right_eye.right_of_right_eyebrow.y) + 70);
                                this.drawBlurImage(contextImg, x, y, width, height);
                            }
                        }
                    }
                    setTimeout(() => {
                        let canvasData = canvasImg.toDataURL('image/jpeg', 1.0);
                        this.captures = canvasData;
                        this.saveImage(canvasData);
                        this.$refs.topProgress.done();
                        this.overlay = 'remove-overlay';
                    }, 1000);
                } else {
                  console.log('no cord found');
                    this.$refs.topProgress.done();
                    this.overlay = 'remove-overlay';
                    this.message= 'Sorry facial recognition has failed. Please try again or continue without facial blur. \nTIP: If you are wearing glasses or any other object is obscuring your face, please remove if you wish to use our privacy protection technology.';
                    this.$refs.myModalRef.show();
                    // this.isHide = false;
                    this.sowUseImageButton = true;
                    this.buttonText = "Retake";
                }
            },

            drawBlurImage(context, x, y, w, h) {
                setTimeout(() => {
                    context.drawImage(this.blurImage, x, y, w, h);
                }, 1000);
            },

            saveImage(canvasData){
              if (this.userType === 'adult') {
                console.log(this.side);
                let position = this.side === 'front' ? 'frontImage' : 'sideImage';
                saveMethods.validateForm(position, 'adult');
                this.side === 'front' ? saveMethods.adult.frontImage(canvasData)
                  : saveMethods.adult.sideImage(canvasData);
              } else {
                saveMethods.validateForm('frontImage', 'kid');
              }
            },

            goBack() {
                this.$router.go(-2);
            },
            useImage() {
                this.$refs.myModalRef.hide();
              setTimeout(() => {
                let canvasData = this.captures;
                this.saveImage(canvasData);
                this.overlay = 'remove-overlay';
              }, 1000);
            },
             hideModal() {
                this.isHide = false;
                this.$refs.myModalRef.hide()
            }
        },
        created() {
            let data = JSON.parse(saveMethods.getLocalStorage('userType'));
            this.userType = data ? data.userType : '';
            this.side = saveData.imagePosition;
            console.log(this.side);
        }
    }
</script>

<style lang="scss" scoped>
    .page {
        text-align: center;
        color: #2c3e50;
    }

    #video {
        background-color: #000000;
    }

    #canvas {
        display: none;
    }

    li {
        display: inline;
        padding: 5px;
    }

    .hide-img {
        position: absolute;
        z-index: -100;
        height: 0;
        width: 0;
    }

    .round-button button {
        font-size: 23px !important;
        padding: 27px 20px !important;
        &.done {
            padding: 26px 16px !important;
        }
    }
    @media screen and (max-width: 512px){
        #video, #canvas {
           // height:400px;
        }
    }
    @media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
        .round-button button {
            padding: 27px 17px !important;
        }
    }
    .d-block p{
        white-space: pre-wrap;
    }
    .page-container .round-button .retakeLink{
        background-color: unset;
        border: unset;
        text-decoration: underline;
    }
    .smm-text{
        text-align: center;
        color:#8b8a8a;
    }
</style>
